@charset "utf-8";

.form{
  display: flex;
}
.form input{
  flex:1;
  font-size:1.25rem;
  border:none;
  outline:none;
  border-bottom:1px solid rgb(0, 140, 255);
}
.create_btn{
  padding:1rem 1.2rem;
  margin-left:1rem;
  border-radius: 3px;
  color:#fff;
  font-weight: bold;
  font-size:1.1rem;
  background-color: rgb(6, 123, 219);
  border:none;
}
.create_btn:hover{
  background:rgb(0, 140, 255);
}