@charset "utf-8";

.todo_item{
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: 220%;
  font-size:1.2rem;
  color:#333;
}
.todo_item:hover{
  font-weight: 600;
}
.todo_item:hover .remove{
  opacity:1;
}
.remove{
  opacity: 0;
  color:rgb(0, 140, 255);
  margin-inline:0.5rem;
  border: 1px solid rgb(0, 140, 255);
  border-radius: 50%;
  width:1.5rem;
  height:1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.todo_text{
  flex:1;
  
}
.checked{
  text-decoration: line-through;
  color:#ccc;
}
.check_mark{
  font-size: 1.2rem;
  line-height: 1rem;
  color:#ccc;
  font-weight: bold;
  text-decoration:none !important;
}